import { cloneDeep, isEmpty, merge, set } from 'lodash-es';
import scheme from './scheme';

//@ts-ignore
function traverseAndFlatten(currentNode, target, flattenedKey) {
	for (var key in currentNode) {
		if (currentNode.hasOwnProperty(key)) {
			var newKey;
			if (flattenedKey === undefined) {
				newKey = key;
			} else {
				newKey = flattenedKey + '.' + key;
			}

			var value = currentNode[key];
			if (typeof value === "object") {
				traverseAndFlatten(value, target, newKey);
			} else {
				target[newKey] = value;
			}
		}
	}
}

//@ts-ignore
function flatten(obj) {
	const flattenedObject = {};
	//@ts-ignore
	traverseAndFlatten(obj, flattenedObject);
	return flattenedObject;
}

const isSchema = (value: unknown): value is string => typeof value === 'string' ? value.startsWith('#/components/schemas/') : false;

type InferencerType = 'create' | 'list' | 'show' | 'edit';

interface ResourceSchema {
    properties: Record<string, any>;
    required: string[];
    type: 'object';
}

interface CrudSchemas {
    [k: string]: Record<'request' | 'response', ResourceSchema | undefined>;
    create: Record<'request' | 'response', ResourceSchema | undefined>;
    edit: Record<'request' | 'response', ResourceSchema | undefined>;
    show: Record<'request' | 'response', ResourceSchema | undefined>;
}

const operationKeys = [['get', 'show'], ['post', 'create'], ['put', 'edit']];

const getCrudSchemas = (resourceSchema: any): CrudSchemas => {
    const crudSchemas: CrudSchemas = {
        create: {
            request: undefined,
            response: undefined,
        },
        edit: {
            request: undefined,
            response: undefined,
        },
        show: {
            request: undefined,
            response: undefined,
        },
    };

    operationKeys.forEach(([operationKey, crudKey]) => {
        if (operationKey in resourceSchema) {
            const flattenSchema = flatten(resourceSchema[operationKey]);

            const requestRef = Object.entries(flattenSchema)
                .find(([key, value]) => key.includes('requestBody.content') && isSchema(value)) as [string, string] | undefined;

            if (requestRef) {
                const requestRefName = requestRef[1].split('/').at(-1)!;
                set(crudSchemas, `${crudKey}.request`, cloneDeep<ResourceSchema>((scheme.components.schemas as any)[requestRefName]));
            }

            const responseRef = Object.entries(flattenSchema)
                .find(([key, value]) => key.includes('responses') && isSchema(value)) as [string, string] | undefined;

            if (responseRef) {
                const responseRefName = responseRef[1].split('/').at(-1)!;
                set(crudSchemas, `${crudKey}.response`, cloneDeep<ResourceSchema>((scheme.components.schemas as any)[responseRefName]));
            }
        }
    });

    return crudSchemas;
};

export const getSchemaByType = (name: string, type: InferencerType): CrudSchemas[InferencerType] => {
    const crudSchemas = {} as CrudSchemas;

    Object.keys(scheme.paths)
        .filter((path) => path.includes(name))
        .forEach((path) => {
            // @ts-ignore
            const result = getCrudSchemas(scheme.paths[path]);
            merge(crudSchemas, result);
            crudSchemas.list = result.show;
        });

    console.log('crudSchemas: ', crudSchemas);

    console.groupEnd();

    return crudSchemas[type];
};
